"strict mode";
import vuex from 'vuex'
import vueStrap from 'vue-strap'
import VueRouter from 'vue-router'

import messages from '../vue/messages.vue'
import captcha from '../vue/captcha.vue'
import loadHtml from '../vue/loadHtml.vue'


import Layout from '../vue/routes/layout.vue'



/*!
 * Version: 0.1.0
 * Author : LianYue
 * URL    : http://www.otamoe.com
 */

(function(Vue, Window, Document) {
    Vue.config.debug = true;
    Vue.config.async = false
    Vue.config.devtools = true



    // starp 组件
    for (var id in vueStrap) {
        if (vueStrap.hasOwnProperty(id)) {
            Vue.component(id == 'option' || id == 'select' ? 'v-' + id : id, vueStrap[id]);
        }
    }


    var components = {
        messages,
        captcha,
        loadHtml,
    };

    for (var id in components) {
        if (components.hasOwnProperty(id)) {
            Vue.component(id, components[id]);
        }
    }


    Vue.use({
        install: function(Vue, options) {
            // 表单数据
            Vue.formData = function(form, data) {
                data = data || {};
                for (var i = 0; i < form.length; i++) {
                    var input = form[i];
                    if (input.name && (input.type != 'checkbox' || input.checked)) {
                        data[input.name] = input.value;
                    }
                }
                return data;
            };
        },
    });





    // 路由配置
    Window.router = new VueRouter({history: true, transitionOnLoad: true});
    if (router.mode == 'html5') {
        if (Window.location.pathname == '/' && Window.location.hash.substr(0, 3) == '#!/') {
            var pathname = Window.location.hash.substr(2);
            Window.location.hash = '';
            Window.location.pathname = pathname;
            return;
        }
    } else {
        if (Window.location.pathname != '/' && Window.location.hash.substr(0, 3) != '#!/') {
            Window.location.hash = '#!' + Window.location.pathname;
            Window.location.pathname = '/';
            return;
        }
    }


    Vue.use({
        install: function(Vue, options) {
            var components = {};

            var parseComponent = function(contents) {
                try {
                    return (new Function('var module = {exports: {}}; var exports = module.exports;   var result = (function(){ '+ contents +'; })();  return typeof result == "object" ? result : module.exports;'))();
                } catch (e) {
                    console.error(e);
                    return false;
                }
            }


            var useSelf = false;

            Vue.getComponent = function(view, complete) {
                var _self = this;



                if (!components[view] && Window.localStorage['vue-' + view]) {
                    var storage = JSON.parse(Window.localStorage['vue-' + view]);
                    components[view] = {};
                    if (storage[0]) {
                        components[view] = parseComponent(storage[0]);
                    }
                    if (storage[1]) {
                        components[view].template = storage[1];
                    }
                }

                var parseBody = function(contents) {
                    if (typeof contents != 'string' && !(contents instanceof Node)) {
                        complete.call(_self, components[view] ? contents : {messages:[{message:'Response data'}], success: false}, components[view] || false);
                        return;
                    }

                    if (typeof contents == 'string') {
                        var element = Document.createElement('div');
                        element.innerHTML = contents;
                    } else {
                        var element = contents;
                    }


                    var el = element.querySelector('#vue');
                    if (!el) {
                        complete.call(_self, {messages:[{message:'Response data'}], success: false}, false);
                        return;
                    }

                    var data = JSON.parse(el.querySelector('[type="application/json"]').textContent);
                    var component = {};

                    var componentElement = el.querySelector('[type="application/vue"]');
                    var templateElement = el.querySelector('template');
                    var styleElement = el.querySelector('style');

                    if (componentElement) {
                        component = parseComponent(componentElement.textContent);
                    }


                    if (templateElement) {
                        component.template = templateElement.innerHTML;
                    }

                    if (!component.template) {
                        complete.call(_self, {messages:[{message:'Response data'}], success: false}, false);
                        return;
                    }


                    Window.localStorage['vue-' + view] = JSON.stringify([
                        componentElement ? componentElement.textContent : '',
                        templateElement ? templateElement.innerHTML : '',
                        styleElement ? styleElement.textContent : '',
                    ]);

                    components[view] = component;

                    complete.call(_self, data, components[view]);

                    element = undefined;
                    el = undefined;
                    templateElement = undefined;
                    componentElement = undefined;
                }


                if (!useSelf) {
                    useSelf = true;
                    parseBody(Document);
                    return;
                }


                var callback = function(response) {
                    if (!response.status) {
                        complete.call(_self, false, false);
                        return;
                    }
                    parseBody(response.data);
                }


                var url = Window.location.protocol + '//' + Window.location.hostname;
                if (router.mode == 'html5') {
                    url += Window.location.pathname + Window.location.search;
                } else if (Window.location.hash && Window.location.hash.substr(0, 3) == '#!/') {
                    url += Window.location.hash.substr(2);
                }


                url += url.indexOf('?') == -1 ? '?' : '&';
                url += 'pjax=true';

                var headers = {
                    'X-Pjax': true,
                };

                if (components[view]) {
                    headers['X-Json'] = true;
                    url += '&json=true';
                }

                Vue.http({url:url, method:'GET', timeout: 20000, headers : headers}).then(callback, callback);
            }
        }
    });



    (function(router) {
        var stringifyPath = router.stringifyPath;
        var a = Document.createElement('a');
        router.stringifyPath = function(path) {
            if (path && typeof path == 'string' && (path.substr(0, 2) == '//' || path.indexOf(':') != -1)) {
                a.href = path;
                path = a.pathname + a.search  + a.hash;
            }
            return stringifyPath.call(this, path);
        };
    })(router);

    router.beforeEach(function() {
        window.scrollTo(0,0)
    })

    Document.addEventListener('DOMContentLoaded', function() {
        var maps = [
            '/account/login',
            '/account/create',
            '/account/lostPassword',
        ];



        for (var i = 0; i < maps.length; i++) {
            (function(view) {
                router.on(view, {
                    component: function(resolve, reject) {
                        Vue.getComponent.call(this, view, function(data, component) {
                            if (!data) {
                                // 超时
                            }

                            if (!component) {
                                // 错误
                            }


                            component.route = component.route || {};
                            var setData = function(data) {
                                if (data.title) {
                                    Document.title = data.title.join(' - ');
                                }

                                if (!(data.results instanceof Array)) {
                                    for (var key in data.results) {
                                        if (!this.results || !this.results[key] || typeof this.results[key] != 'object' || typeof this.results[key].value == 'undefined') {
                                            continue;
                                        }

                                        if (!data.results[key] || typeof this.results[key] != 'object' || typeof data.results[key].value != 'undefined') {
                                            continue;
                                        }
                                        data.results[key].value = this.results[key].value;
                                    }
                                }

                                this.$data = data;
                            };


                            component.route.data = function(transition) {
                                if (data) {
                                    setData.call(this, data);
                                    data = false;
                                    transition.next();
                                } else {
                                    Vue.getComponent.call(this, view, function(data, component) {
                                        if (data && component) {
                                            setData.call(this, data);
                                            transition.next();
                                        }
                                    });
                                }
                            }

                            resolve(component);
                        });
                    }
                });
            })(maps[i]);
        }

        router.start(Layout, '#app');
    }, false);
})(Vue, window, document);



