<template>
    {{{data}}}
</template>

<script>
export default {
    props: ['url'],
    data : function() {
        return {data:''};
    },
    watch: {
        url: function(value, oldValue) {
            if (value != oldValue) {
                this.load();
            }
        }
    },

    methods: {
        load: function() {
            var _self = this;
            var url = _self.url;
            if (!url) {
                _self.data = '';
            }
            url += url.indexOf('?') == -1 ? '?' : '&';
            url += 'pjax=true';
            this.$http({url: url, headers: {'X-Pjax': true}, timeout: 15000}).then(function(response) {
                _self.data = response.data;
            });
        }
    },

    created: function() {
        this.load();
    },
}
</script>



